import template from './commentTable.html';
import CommentResponse from '../../../Http/Responses/commentResponse';
import Environment from '../../../Config/environment';
import AvatarDriverRegistry from '../../../Extend/Avatars/avatarDriverRegistry';
import Author from '../../../Data/Comments/author';

export default {
  template: template,
  props: {
    loading: {
      type: Boolean,
      default: false
    },
    comments: {
      type: CommentResponse,
      default: null
    }
  },
  data() {
    return {
      avatarDriver: null
    };
  },
  methods: {
    getAuthor(comment: Comment): Author {
      return {
        author: this.comments.getAuthor(comment.authorId)
      };
    }
  },
  created() {
    if (AvatarDriverRegistry.hasDriver(Environment.Settings.avatarDriver)) {
      this.avatarDriver = AvatarDriverRegistry.getDriverName(Environment.Settings.avatarDriver);
    } else {
      this.avatarDriver = AvatarDriverRegistry.getDriverName(AvatarDriverRegistry.DefaultDriverName);
    }
  }
};
