import template from './loader.html';
import Type from '../../../Types/type';

export default {
  template: template,
  props: {
    displayText: {
      type: String,
      default: ''
    },
    size: {
      type: Number
    },
    displayInline: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    computedSize() {
      if (Type.hasValue(this.size)) {
        return this.size;
      }

      return this.displayInline ? 16 : 24;
    }
  }
};
