import Endpoints from '../Http/endpoints';
import Client from '../Http/client';
import CommentResponse from '../Http/Responses/commentResponse';
import ErrorResponse from '../Http/Responses/errorResponse';
import SearchOptions from '../Data/Comments/searchOptions';
import Type from '../Types/type';

class CommentRepository {

  constructor() {
    this.client = new Client();
  }

  search(options: SearchOptions): Promise<CommentResponse> {
    if (Type.hasValue(options) === false) {
      options = new SearchOptions();
    }

    return new Promise(function (resolve, reject) {
      this.client.get(Endpoints.url(Endpoints.COMMENTS_SEARCH), options).then(function (result) {
        resolve(CommentResponse.fromApiResponse(result));
      }).catch(function (e) {
        reject(ErrorResponse.fromError(e));
      });
    }.bind(this));
  }

  unpublish(commentId: string) {
  }

  unpublishMany(commentIds: Array<string>) {
  }

  publish(commentId: string) {
  }

  publishMany(commentIds: Array<string>) {
  }

  update(commentId: string, data: any) {
  }

  markAsSpam(commentId: string) {
  }

  markManyAsSpam(commentIds: Array<string>) {
  }

  markAsNotSpam(commentId: string) {
  }

  markManyAsNotSpam(commentId: Array<string>) {
  }

}

export default CommentRepository;
